    /***
     * 增加同级或子级
     * @param curId 当前选中的节点ID
     * @param sourceData tableData数据对象
     * @param targetData 新增加或修改的对象内容
     * @param layer 1是同级，2是子级
     * @param flag 1是新增，2是修改，3是删除。
     *
     */

    checkPermissionId(curId,sourceData,targetData,layer,flag){
      console.info("进入checkPermissionId:"+sourceData)
      console.info("查询选中ID:"+curId);
      var result = false;
      for(var i1=0;i1<sourceData.length;i1++){
        console.info("当前id:"+sourceData[i1].id);
        if(flag==='1'){
          if(sourceData[i1].id===curId ){
            if(layer==='1'){//同级
              sourceData.push(targetData);
              console.info("add find ok");
              result = true;
              break;
            }else if (layer==='2'){
              if(sourceData[i1].hasOwnProperty("detail")){
                console.info("add 存在detail,直接追加:"+JSON.stringify(sourceData[i1]))
                let detail = sourceData[i1].detail;

                detail.push(targetData);
                sourceData[i1].detail = detail;
                result = true;
                break;
              }else{
                let detail = [];

                detail.push(targetData);
                sourceData[i1].detail = detail ;
                console.info("add 不存在detail,新创建detail:"+JSON.stringify(sourceData[i1].detail));
                result = true;
                break;
              }
            }
            //break;
          } else{
            if(sourceData[i1].hasOwnProperty("detail")){
              console.info("add not find 递归查询detail")
              result = this.checkPermissionId(curId,sourceData[i1].detail,targetData,layer,flag);
              if(result===true){
                break;
              }
            }else{
              console.info("add not find");
              result = false;
            }
          }

        }//end if(flag==='1'){
        else  if(flag==='2'){
          if(sourceData[i1].id===curId ){
            sourceData[i1].caption = targetData.caption;

            console.info("modify find ok");
            result = true;
            break;
          }else{
            if(sourceData[i1].hasOwnProperty("detail")){
              console.info("modify not find 递归查询detail")
              result = this.checkPermissionId(curId,sourceData[i1].detail,targetData,layer,flag);
              if(result===true){
                break;
              }
            }else{
              console.info("modify not find");
              result = false;
            }
          }
        }else if(flag==='3'){
          if(sourceData[i1].id===curId ){
            sourceData.splice(i1, 1);
            console.info("del find ok");
            result = true;
            break;
          }else{
            if(sourceData[i1].hasOwnProperty("detail")){
              console.info("del not find 递归查询detail")
              result = this.checkPermissionId(curId,sourceData[i1].detail,targetData,layer,flag);
              if(result===true){
                break;
              }
            }else{
              console.info("del not find");
              result = false;
            }
          }
        }


      }
      return result;

    },
    allowDropForTree(draggingNode, dropNode, type){
      //注意：同一级中上级不能向下级拖，下级可以向上级拖；不同级中上级可以向不同级中的下级拖动
      console.log("draggingNode.data.type:"+draggingNode.data.type);
      console.log("dropNode.data.type:"+dropNode.data.type);
      console.log("type:"+ type);
      console.log("dropNode.level:"+ dropNode.level);
      //拖动源时，不允许第1层节点拖动;拖动到目标时，不允许拖动到第1层节点prev之前或next之后（但允许拖到到第1拖节点的里面inner）
      if (draggingNode.level === 1 || (dropNode.level===1 && type!=="inner") ){
        return false;
     //   return type !== 'inner';
          // 向上拖拽type === "prev" || 向下拖拽 || type === "next";
          //return type === "prev" || type === "next";
      } else {
        //false是不可以拖，true是可以拖

        return true;

      }
    },
    handleDropForTree(draggingNode, dropNode, dropType, ev) {
      //level是从1开始
      console.log('拖动到树节点【'+ dropNode.label+"】的位置："+ dropType+",draggingNode.level:"+draggingNode.level+",dropNode.level:"+dropNode.level);
      //dropType为before之前，after之后，inner里面。
      if(dropType==="before" || dropType==="after"){
        draggingNode.data.type = parseInt(dropNode.data.type) ;
      }else if(dropType==="inner"){
        draggingNode.data.type = parseInt(dropNode.data.type) + 1;
      }
    },
