    getQqueryParams(url){
      let params = {};
      if (url) {
        let queryArray =  url.split('?');
        if (queryArray.length > 1) {
          let query = queryArray[1];
          let array = query.split('&');
          array.map((value) => {
            let valueArray = value.split('=');
            if (valueArray.length > 1) {
              // 还需要对value进行解码（可能涉及到在value为中文字符，在赋值到二维码前进行了encodeURI编码）
              // Object.assign(params, { [valueArray[0]]: decodeURI(valueArray[1]) })
              Object.assign(params, { [valueArray[0]]: valueArray[1] });

            }
          })
        }
      }
      return params;
    },
  }
}
</script>

